*,
*::before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "sans-serif";
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(0deg, white 65%, var(--primary) 94%);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
  color: var(--text-color-accent);
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  --breakpoint-xl: 1920px;
  width: 85%;
  max-width: 1020px;
  margin-right: auto;
  margin-left: auto;
}

.main-hotel-info .container {
  padding-left: 1rem;
}

.articles {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 6rem;
}

@media (max-width: 768px) {
  .articles {
    grid-template-columns: 1fr;
  }
}

.principles__title {
  font-size: 1rem;
  margin-bottom: .60rem;
}

.principles-atractions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: .55rem;
}

.discover__title {
  color: var(--text-color-accent);
  line-height: 1.6;
}

.discover__info {
  line-height: 1.6;
  margin-bottom: 1rem;
}

.main-footer {
  margin-top: auto;
  background-color: var(--light-blue);
  text-align: center;
  font-size: .85rem;
  padding-bottom: .55rem;
}

.main-footer__links {
  display: flex;
  justify-content: center;
}

@media (max-width: 767px) {
  .main-footer__links .nav {
    align-items: center;
    flex-direction: column;
  }
}

.main-footer__links .nav__item {
  text-transform: uppercase;
}

.main-footer__links .nav__item::after {
  content: '';
  width: 1px;
  height: 3px;
}

.main-footer__about {
  display: flex;
  justify-content: center;
}

@media (max-width: 767px) {
  .main-footer__about {
    align-items: center;
    flex-direction: column;
  }
}

.main-footer__about p:first-child {
  margin-right: .55rem;
}

.hotel-info {
  padding-top: 1rem;
  padding-bottom: .45rem;
}

.hotel-info__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
}

.main-header {
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  position: relative;
}

.main-header__slider {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 85%;
  max-width: 1020px;
  margin: 0 auto;
}

.main-header__slider img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity .5s ease-in;
}

.main-header__slider img + img {
  opacity: 0;
}

.main-header__content {
  position: relative;
}

.main-hotel-info {
  background: var(--primary);
  background: linear-gradient(-86deg, rgba(255, 255, 255, 0) 33%, var(--primary) 77%);
}

.services {
  padding: 3rem 0;
}

.services__list {
  display: flex;
  list-style-type: none;
  text-align: center;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  .services__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .services__list {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.services__item {
  padding: 1.3rem 2.5rem;
  white-space: nowrap;
}

.services__item i {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: var(--primary);
}

@media (max-width: 768px) {
  .services__item {
    white-space: normal;
  }
}

[class^="button--"],
.button {
  --background-color: #cceecc;
  --color-button: #000;
  border: 0;
  background: var(--background-color);
  color: var(--color-button);
  padding: 0 .30rem;
  border-radius: .25rem;
  line-height: 2;
  font-size: .80rem;
  font-weight: bold;
}

.button--primary {
  --background-color: var(--secondary);
  --color-button: #FFF;
}

.button--cta {
  --background-color: linear-gradient(0deg, rgba(6,142,203,1) 44%, rgba(10,195,254,1) 100%);
  --color-button: #FFF;
  font-size: 1rem;
  padding: .45rem;
}

.nav {
  display: flex;
  list-style-type: none;
}

.nav__item {
  padding: 1rem;
  transition: all .3s ease-in-out;
}

.nav--principal {
  background: #FFF;
  width: max-content;
  position: relative;
  top: 25px;
}

.nav--principal .nav__item {
  color: var(--secondary);
}

.nav--principal .nav__item:hover {
  color: #FFF;
  background-color: var(--secondary);
}

.card-discover {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border-radius: .25rem;
  overflow: hidden;
  border: 1px solid var(--light-blue);
}

.card-discover__title {
  font-size: 2.2rem;
  font-weight: 600;
}

.card-discover__description {
  padding-left: 1rem;
  padding-top: 1rem;
  background-image: linear-gradient(to bottom, #f9faef, var(--light-blue));
}

.card-discover__info {
  font-size: 2rem;
}

.card-discover img {
  height: 100%;
  object-fit: cover;
}

.card-principles {
  display: grid;
  grid-template-columns: .7fr 1fr;
  background-color: var(--light-blue);
  border-radius: .25rem;
  overflow: hidden;
}

.card-principles__image {
  height: 100%;
  object-fit: cover;
}

.card-principles__body {
  padding: .4rem;
  line-height: 1.3;
}

.card-principles__title {
  font-size: .90rem;
}

.card-principles__price {
  font-weight: bold;
  font-size: 1.4rem;
  color: var(--text-color-accent);
}

.search-box {
  margin-top: 40px;
  padding-left: 1rem;
  display: flex;
  justify-content: space-between;
}

.hotel-form-reservation {
  color: #FFF;
  display: grid;
  grid-template-columns: repeat(2, 230px);
  grid-gap: 1rem;
  padding: 1.2rem 0;
  padding-right: 1rem;
}

.hotel-form-reservation__item:first-child {
  grid-column: 1 / -1;
}

.box-room {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.box-room__pax {
  margin-bottom: 1rem;
}

.box-room__pax-item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.ofert {
  --yellow: #e4d05a;
  position: relative;
  width: 210px;
  height: 280px;
  background: rgba(228, 208, 90, 0.8);
  border-radius: 23% 16% 26% 31% / 13% 15% 28% 28%;
  transform: rotate(8deg) translateX(-6%);
}

.ofert .button--cta {
  margin: 1rem 0;
}

.ofert__content {
  font-size: 1.2rem;
  text-align: center;
  padding: 1.5rem;
  transform: rotate(-8deg);
  line-height: 1.6;
}

.ofert__title {
  font-size: 1.2rem;
  text-transform: uppercase;
  white-space: nowrap;
}

.ofert__price-icon {
  white-space: nowrap;
  font-size: 2.2rem;
}

.ofert__price {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-color-accent);
}

.ofert__band {
  position: absolute;
  top: -50%;
  left: 66%;
  transform: rotate(45deg);
}

.ofert__band div:first-child {
  width: 13px;
  height: 45px;
  transform: rotate(85deg);
  background: rgba(228, 208, 90, 0.8);
  border-radius: 10px;
}

.ofert__band div:nth-child(2) {
  width: 15px;
  height: 15px;
  background: rgba(228, 208, 90, 0.8);
  border-radius: 100%;
  transform: rotate(120deg) translate(-200%, -148%);
  box-shadow: 13px 15px 0px 0px rgba(228, 208, 90, 0.8);
}

.ofert__band div:nth-child(3) {
  width: 15px;
  height: 15px;
  background: rgba(228, 208, 90, 0.8);
  border-radius: 100%;
  transform: rotate(120deg) translate(-270%, -250%);
  box-shadow: 13px 15px 0px 0px rgba(228, 208, 90, 0.8);
}

.ofert__band div:last-child {
  width: 15px;
  height: 15px;
  background: rgba(228, 208, 90, 0.8);
  border-radius: 100%;
  transform: rotate(45deg) translate(296%, -250%);
  box-shadow: 13px 15px 0px 0px rgba(228, 208, 90, 0.8), -13px -15px 0px 0px rgba(228, 208, 90, 0.8);
}

.tab {
  background: var(--terceary);
}

.tab__title {
  background: var(--primary);
  color: var(--secondary);
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  padding: .35rem 0;
}

.tab__panels, .tab__options {
  padding-left: .55rem;
}

.tab__options {
  display: flex;
  list-style-type: none;
}

.tab__option-item {
  padding-top: 1.6rem;
  overflow: hidden;
}

.tab__option-item.active {
  background-image: url("https://s3.amazonaws.com/cdn.henryraygan.com/portfolio/arrow.png");
  background-size: 90% 1.3rem;
  background-repeat: no-repeat;
  background-position: top;
}

.tab__option-item.active .tab__option-button {
  color: var(--secondary);
  background: var(--primary);
}

.tab__option-button {
  border: 0;
  background: transparent;
  padding: .25rem;
  font-size: .88rem;
  margin-right: .25rem;
  border-radius: .25rem;
  cursor: pointer;
  color: var(--primary);
  font-weight: 500;
}

.tab__option-button:focus {
  outline: none;
}

.form__input {
  background-color: var(--primary);
  border: none;
  padding: .25rem .35rem;
  color: #FFF;
}

.form__input:focus {
  outline: 0;
}

.form__label {
  margin-bottom: .2rem;
  line-height: 1.6;
}

.form__group {
  display: flex;
  flex-direction: column;
}

.form__select {
  background: var(--primary);
  border: none;
  color: #FFF;
}

.bold-big-size {
  font-weight: 500;
  font-size: 1.2rem;
}

.logo {
  display: flex;
  align-items: flex-end;
}

.logo__image {
  width: 90px;
  margin-right: .6rem;
}

.logo__text {
  color: #fff;
  text-transform: uppercase;
  font-size: .88rem;
  font-weight: 500;
}

.logo__text span {
  font-size: 1.6rem;
  font-weight: bold;
}

.central-reservations-number {
  color: var(--text-accent-color);
  font-size: 1rem;
}

.number-central {
  font-size: 2rem;
}
